/* PageStyles.css */
.page {
  padding: 4rem 2rem;
  text-align: center;
  background: radial-gradient(ellipse at top, #111 0%, #0a0a0a 100%);
  color: #eaeaea;
  min-height: 100vh;
  overflow-x: hidden;
}

.about-section h2 {
  font-size: 2.8rem;
  color: #00ffe7;
  margin-bottom: 1rem;
  animation: floatGlow 2s ease-in-out infinite;
}

.glow {
  text-shadow: 0 0 10px #00ffe7, 0 0 20px #00ffe7;
}

.profile-img {
  width: 160px;
  border-radius: 50%;
  border: 3px solid #00ffe7;
  margin: 2rem 0;
  box-shadow: 0 0 30px rgba(0, 255, 231, 0.3);
  transition: transform 0.4s ease;
}
.profile-img:hover {
  transform: scale(1.05) rotate(2deg);
}

.bio {
  max-width: 700px;
  margin: auto;
  font-size: 1.1rem;
  line-height: 1.7;
  color: #bbb;
}

.quote {
  font-style: italic;
  color: #888;
  margin-top: 1.5rem;
}

.typewriter {
  font-family: 'Courier New', monospace;
  color: #7fffd4;
  font-size: 1.3rem;
  margin-bottom: 2rem;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid #00ffe7;
  width: 0;
  animation: typing 3s steps(40, end) forwards, blink 0.7s step-end infinite;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 24ch;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

@keyframes floatGlow {
  0%, 100% {
    text-shadow: 0 0 10px #00ffe7;
  }
  50% {
    text-shadow: 0 0 20px #00ffe7;
  }
}
